
import { defineComponent, reactive } from 'vue'
import router from './../router'
import { Dialog } from 'vant'

export default defineComponent({
  setup () {
    const state = reactive({
      value: '',
      showPicker: false
    })
    const toPage = () => router.push('/order')
    const toPageReturn = () => router.go(-1)
    const submit = () => {
      Dialog.confirm({
        title: '确认',
        message: '是否确认该意见已处理？'
      }).then(() => {
        // on confirm
      }).catch(() => {
        // on cancel
      })
    }
    return {
      state,
      toPage,
      submit,
      toPageReturn
    }
  }
})
